@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
html {
  scroll-behavior: smooth;
}
body {
  width: 100%;
  height: 100%;
  background: #1f2028;
  overflow-x: hidden;
  visibility: hidden;
}

@font-face {
  font-family: "Neue Machina Bold";
  src: url(/static/media/NeueMachina-Ultrabold.07d4eef9.otf) format("OpenType");
}

@font-face {
  font-family: "Neue Machina Regular";
  src: url(/static/media/NeueMachina-Regular.6c408aee.otf) format("OpenType");
}
@font-face {
  font-family: "Neue Machina Light";
  src: url(/static/media/NeueMachina-Light.e1fb3b25.otf) format("OpenType");
}
a {
  color: inherit;
  text-decoration: none;
}

li {
  list-style-type: none;
}
*,
*::after,
*::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.keen-slider__slide {
  max-height: 600px !important;
  max-width: 1280px !important;
  width: 100% !important;
  min-height: 250px !important;
  height: 100% !important;
}

